@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Yantramanav:wght@100;300;400;500;700;900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Noto+Serif:ital,wght@0,400;0,700;1,400;1,700&display=swap);
/* font-family: 'Yantramanav', sans-serif; */
/* font-family: 'Noto Serif', serif; */

*, *::before, *::after {
  margin: 0;
  box-sizing: border-box;
}

:root {
  --themeColorPrimary: #E84A43;
  --themeColorAccent: rgb(185, 192, 213);
  --themeColorAccentNeg10: rgb(95 104 131);
  --themeColorDark: rgba(50, 50, 50);
  --themeColorDarkNeg10: rgba(75, 75, 75);

  font-family: 'Poppins', sans-serif;
  font-size: calc(1vw + 16px);
  color: white;
  background-color: rgba(50, 50, 50);
  background-color: var(--themeColorDark);

  --navHeight: 3rem;
}

h1, h2, h3, h4, h5, h6 {
  font-weight: normal;
}

a {
  text-decoration: none;
  color: inherit;
}
.nav {
    display: flex;
    width: 100%;
    font-size: .5rem;
    justify-content: right;
    padding: 0 .2rem;
    align-items: center;
    height: var(--navHeight);
    position: fixed;
    -webkit-transform: translateZ(0);
            transform: translateZ(0);
    z-index: 100;
    opacity: 1;
    transition:
        background-color .2s,
        opacity .4s;
}

.nav--opaque {
    background-color: var(--themeColorDark);
    box-shadow: 0 0 8px 0 rgba(35, 35, 35, 1);
}

.nav--hidden {
    opacity: 0;
}

.nav_nonlink,
.nav a {
    display: inline;
    color: white;
    margin-right: 1rem;
    transition: color .3s;
    padding: .5rem 0rem;
}

a.nav_link--active {
    color: var(--themeColorPrimary);
}

.nav a:hover {
    color: var(--themeColorPrimary);
}

.nav_contactbutton {
    border: 1.5px solid white;
    padding: .2rem .6rem;
    border-radius: 5px;
    transition:
        background-color .3s,
        border-color .3s;
}

.nav_contactbutton:hover {
    cursor: pointer;
    background-color: var(--themeColorPrimary);
    border-color: var(--themeColorPrimary);
    color: var(--themeColorDark);
}
.navDropdown_sub {
    display: none;
    position: absolute;
    flex-direction: column;
    text-align: right;
    box-shadow: 0 0 .5rem 0 rgba(0, 0, 0, .5);
    background-color: var(--themeColorDark);
    padding: .4rem;
    border-radius: 2px;
    margin-top: .4rem;
    /*translateX is handled in js*/
}

.navDropdown_sub > a {
    margin: 0;
    padding: .1rem 0;
}

.navDropdown:hover > .navDropdown_sub {
    display: flex;
}
/*
ANIMATIONS
*/

@-webkit-keyframes fadeFromLeft {
    0% {
        margin-left: -1rem;
        opacity: 0;
    }
    20% {
        margin-left: -1rem;
        opacity: 0;
    }
    100% {
        margin-left: 0;
        opacity: 1;
    }
}

@keyframes fadeFromLeft {
    0% {
        margin-left: -1rem;
        opacity: 0;
    }
    20% {
        margin-left: -1rem;
        opacity: 0;
    }
    100% {
        margin-left: 0;
        opacity: 1;
    }
}

/*
SPLASH
*/

