}

.homeSplash > h1 {
    font-size: 2rem;
    margin-bottom: 1.5rem;
    -webkit-animation-name: fadeFromLeft;
            animation-name: fadeFromLeft;
    -webkit-animation-duration: 2s;
            animation-duration: 2s;
}

.homeSplash > p {
    max-width: 30ch;
    margin-bottom: 1rem;
    margin-bottom: 1.2rem;
    -webkit-animation-name: fadeFromLeft;
            animation-name: fadeFromLeft;
    -webkit-animation-duration: 3s;
            animation-duration: 3s;
}

