.getStartedBody_message {
    width: 100%;
}

.getStartedBody_message > textarea {
    width: 100%;
}

.getStartedBody input { 
    outline: none;
    background-color: inherit;
    border: 1px solid gray;
    height: 1.2rem;
    padding-left: 2ch;
    margin-top: .2rem;
    color: white;
    font-family: 'Poppins', sans-serif;
    width: 7rem;
}

.getStartedBody input:focus { 
    border: 1px solid var(--themeColorPrimary);
}

.getStartedBody textarea { 
    outline: none;
    background-color: inherit;
    border: 1px solid gray;
    margin-top: .2rem;
    height: 4rem;
    color: white;
    font-family: 'Poppins', sans-serif;
    padding: 1ch 2ch;
    margin-bottom: .4rem;
}

.getStartedBody input::-webkit-input-placeholder, .getStartedBody textarea::-webkit-input-placeholder {
    color: gray;
}

.getStartedBody input:-ms-input-placeholder, .getStartedBody textarea:-ms-input-placeholder {
    color: gray;
}

.getStartedBody input::placeholder,
.getStartedBody textarea::placeholder {
    color: gray;
}

.getStartedBody textarea:focus {
    border: 1px solid var(--themeColorPrimary);
}

.getStartedBody_submit {
    border: none;
    background-color: var(--themeColorPrimary);
    font-family: 'Poppins', sans-serif;
    color: var(--themeColorDark);
    padding: .25rem 1.25rem;
    transition: opacity .2s;
}

.getStartedBody_submit:hover {
    opacity: .8;
    cursor: pointer;
}
